.description_wrap_desktop{
    width: 100%;
    height: auto;
    background-image: url(../img/background/description_desktop_bg.png);
    background-size: cover;
    background-position: center bottom;

    padding-top: 100px;
    padding-bottom: 100px;

    z-index: -5;
    .content_wrap{
        @include content-width-descr;
        display: flex;
        flex-direction: column;
        align-items: center;
        .img-a-wrap{
            
            .shield_wrap{
                margin: 0 auto;
                width: 50%;

                z-index: 1;

                animation: shield-rotate 50s ease-in-out infinite ;
                .shield{
                    width: 100%;
                }
            }
            @keyframes shield-rotate {
                0%{
                   transform: rotate(0);
                }
                50% {
                    transform: rotate(360deg);
                }
                100%{
                    transform: rotate(0);
                 }
             }
            /* .shield_wrap:hover{
                transform: rotate(180deg);
            } */
            .shield-title{
                position: relative;

                margin: 0 auto;
                width: 50%;

                z-index: 2;
                /* animation: title-scale 15s ease-in-out infinite; */
                .title-img{
                    position: absolute;
                    width: 80%;

                    bottom: 105px;
                    left: 60px;
                }
            }
            /* @keyframes title-scale {
                0%{
                   transform: scale(1);
        
                }
                50% {
                    transform: scale(1.02);
                    
                }
                100%{
                    transform: scale(1);
        
                 }
             } */
            .hulk-wrap{
                position: relative;

                margin: 0 auto;
                width: 55%;

                z-index: 2;
                @include animation-settings;
                transition-delay: .3s;
                a{
                    
                    .hulk-img{
                        position: absolute;
                        width: 50%;
    
                        bottom: -80px;
                        left: 350px;
                    }
                }
            }
            .hulk-wrap:hover{
                transform: scale(1.15) rotate(7deg);
            }
            .superman-wrap{
                position: relative;

                margin: 0 auto;
                width: 55%;

                z-index: 1;
                @include animation-settings;
                transition-delay: .3s;
                a{
                    
                    .super-img{
                        position: absolute;
                        width: 45%;
    
                        bottom: 180px;
                        left: 400px;
                    }
                }
            }
            .superman-wrap:hover{
                transform: scale(1.15) rotate(7deg);
            }
            .spider-wrap{
                position: relative;

                margin: 0 auto;
                width: 60%;

                z-index: 7;
                @include animation-settings;
                transition-delay: .3s;
                a{
                    
                    .spider-img{
                        position: absolute;
                        width: 45%;
    
                        bottom: 0px;
                        left: -30px;
                    }
                    .sp-web{
                        position: absolute;
                        width: 30%;

                        bottom: 0px;
                        left: -80px;

                        opacity: 0;

                        @include animation-settings;
                        transition-delay: .3s;
                    }
                }
            }
            .spider-wrap:hover{
                transform: scale(1.05) rotate(-7deg);
            }
            .spider-wrap .sp-web:hover{
                opacity: 1;
                transform: rotate(-15deg);
            }
            .dragon-wrap{
                position: relative;

                margin: 0 auto;
                width: 55%;

                z-index: 5;
                @include animation-settings;
                transition-delay: .3s;
                a{
                    .light-img{
                        position: absolute;
                        width: 45%;
    
                        bottom: 210px;
                        left: -60px;

                        opacity: 0;
                        z-index: 5;

                        @include animation-settings;
                    }
                    .dragon-img{
                        position: absolute;
                        width: 45%;
    
                        bottom: 210px;
                        left: -60px;

                        @include animation-settings;
                    }
                }
            }
            .dragon-wrap .light-img:hover{
                opacity: 1;
                z-index: 6;

                transform: scale(1.1);
            }
            .dragon-wrap .dragon-img:hover{
                opacity: 0;

                transform: scale(1.1);
            }
            .dragon-img + .light-img:hover{
                opacity: 0;
            }
            /*  */
            .ir-man-wrap{
                position: relative;

                margin: 0 auto;
                width: 75%;

                z-index: 1;
                @include animation-settings;
                transition-delay: .3s;
                a{
                    .ir-man-light-img{
                        position: absolute;
                        width: 45%;
    
                        bottom: 70px;
                        left: -100px;

                        opacity: 1;
                        z-index: 0;

                        @include animation-settings;
                    }
                    .ir-man-img{
                        position: absolute;
                        width: 45%;
    
                        bottom: 70px;
                        left: -100px;

                        @include animation-settings;
                    }
                }
            }
            .ir-man-wrap .ir-man-img:hover{
                opacity: 1;
                z-index: 6;
            }
            .ir-man-wrap .ir-man-img:hover{
                opacity: 0;
            }
            .ir-man-img + .light-img:hover{
                opacity: 0;
            }
            /*  */
            /*  */
            .thor-wrap{
                position: relative;

                margin: 0 auto;
                width: 100%;

                z-index: 0;
                @include animation-settings;
                transition-delay: .3s;
                a{
                    .thor-light-img{
                        position: absolute;
                        width: 45%;
    
                        bottom: 200px;
                        /* left: 620px; */
                        left: 50%;

                        opacity: 0;
                        z-index: 1;

                        @include animation-settings;
                    }
                    .thor-img{
                        position: absolute;
                        width: 45%;
    
                        bottom: 200px;
                        left: 50%;

                        @include animation-settings;
                    }
                }
            }
            .thor-wrap .thor-light-img:hover{
                opacity: 1;
                transform: scale(1.02);
            }
            /*  */
            .logo-link-wrap{
                .link-cont{
                    .logo-img{

                    }
                }
            }
            /*  */
            .minecraft-wrap{
                position: relative;

                margin: 0 auto;
                width: 65%;

                z-index: 0;
                @include animation-settings;
                transition-delay: .3s;
                a{
                    .minecraft-light-img{
                        position: absolute;
                        width: 55%;
    
                        bottom: 370px;
                        left: 200px;

                        opacity: 0;
                        z-index: 5;

                        @include animation-settings;
                    }
                    .minecraft-img{
                        position: absolute;
                        width: 55%;
    
                        bottom: 370px;
                        left: 200px;

                        @include animation-settings;
                    }
                }
            }
            .minecraft-wrap .minecraft-light-img:hover{
                opacity: 1;
                z-index: 6;
            }
            .minecraft-wrap .minecraft-img:hover{
                opacity: 0;
            }
            .dragon-img + .light-img:hover{
                opacity: 0;
            }
            .child_free_wrap{
                margin: 0 auto;
                width: 30%;
                position: relative;

                @include animation-settings;

                z-index: 10;
                .child_free{
                    width: 100%;
                    position: absolute;
                    bottom: 35px;
                }
            }
        }
    }
}